import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ScreenSpinner,
  TabbarItem,
  Epic,
  Div,
  Tabbar,
  ConfigProvider,
  ActionSheet,
  ActionSheetItem,
  Snackbar,
  Avatar,
  CellButton,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import { Icon16Done } from "@vkontakte/icons";
import { Icon16Cancel } from "@vkontakte/icons";

// import Icon16Done from "@vkontakte/icons/dist/16/done";
// import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";

import img1 from "./img/1.png";
import img2 from "./img/2.png";
import img3 from "./img/3.png";
import img4 from "./img/4.png";

import "./styles/reset.sass";
import "./styles/panels.sass";
import "./styles/img.sass";

//constants
import {
  APP_IMG_SHARING_STORIES,
  APP_ID,
  USER_ID,
  GROUP_ID_SUBSCRIPTION_MAIN,
} from "./constants";

import { sharing } from "./sharing-method";
import { nativeAds } from "./ads";
import {
  subscribeMessageFromGroupDefault,
  getUserToken,
} from "./bridge-method";

import { Home, DefaultPanel, ResultPanel, AdminPanel } from "./panels";
import axios from "axios";
import { getRandomInt } from "@vkontakte/vkjs";

window.globalURLSharing = {
  url: 0,
};

const App = () => {
  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState();
  const [IMGresult, setIMGresult] = useState(null);
  const [urlSharing, setUrlSharing] = useState(null);
  const [activeModal, setActiveModal] = useState(null);
  const [snackbar, setSnackbar] = useState(null);
  const [userToken, setUserToken] = useState("");
  const [getPlatform, setGetPlatform] = useState("");
  const [gotToken, setGotToken] = useState(false);
  const [getGroupId, setGetGroupId] = useState({});
  const [getButtonStats, setGetButtonStats] = useState({});

  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "client_light";
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");
      setUser(user);
      setPopout(null);
    }
    fetchData();
  }, []);

  useEffect(() => {
    getUserToken(setUserToken, setGotToken);
    subscribeMessageFromGroupDefault(GROUP_ID_SUBSCRIPTION_MAIN);
  }, []);

  useEffect(() => {
    // Определение ОС
    bridge
      .send("VKWebAppGetClientVersion")
      .then((result) => {
        setGetPlatform(result.platform);
        // getPlatform = result.platform;

        console.log("result.platform", getPlatform);
      })
      .catch((err) => console.log(err));
  }, []);

  const getStats = () => {
    axios
      .get("https://ods-studio.ru/illuminate/get")
      .then(function (response) {
        console.log(response.data);
        setGetGroupId(response.data.links);
        setGetButtonStats(response.data.stats);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  // получение группы с сервера
  useEffect(() => {
    getStats();
  }, []);

  // https://vk.com/dev/wall.post
  // https://vk.com/dev/vk_bridge_events_7
  // https://vk.com/dev/vk_bridge_events_7
  // https://vk.com/dev/stories.getPhotoUploadServer

  function publishStories() {
    let albumID;

    bridge
      .send("VKWebAppCallAPIMethod", {
        method: "photos.createAlbum",
        params: {
          title: "ILLUMINATE TEST",
          description: `Узнай дату первого секса, свадьбы и смерти, пройдя тест в приложении 😅
          Зпускай по ссылке 👇
          https://vk.com/app${APP_ID}`,
          v: 5.21,
          access_token: userToken,
        },
      })
      .then((r) => {
        console.log("photos.createAlbum r.response.id:", r.response.id);
        albumID = r.response.id;
        // albumID = 278233876;
        bridge
          .send("VKWebAppCallAPIMethod", {
            method: "photos.getUploadServer",
            params: {
              album_id: albumID,
              v: 5.21,
              access_token: userToken,
            },
          })
          .then(async (res) => {
            console.log("photos.getUploadServer res:", res);

            const img = [img1, img2, img3, img4][getRandomInt(0, 3)];

            const blob = await (await fetch(img)).blob();
            const formData = new FormData();
            formData.append("file", blob, "image.png");

            // const proxy = "https://26d76bf93c96.ngrok.io";
            // const proxy = "https://proxy.one-digitalspace.ru";
            const proxy = "https://proxy.ods-studio.ru";

            const { data: result } = await axios({
              method: "post",
              url: `${proxy}/${res.response.upload_url}`,
              data: formData,
              headers: { "Content-Type": "multipart/form-data" },
            });

            bridge
              .send("VKWebAppCallAPIMethod", {
                method: "photos.save",
                params: {
                  album_id: albumID,
                  v: 5.21,
                  access_token: userToken,
                  hash: result.hash,
                  photos_list: result.photos_list,
                  server: result.server,
                  caption: `Узнай дату первого секса, свадьбы и смерти, пройдя тест в приложении 😅
                  Зпускай по ссылке 👇
                  https://vk.com/app${APP_ID}`,
                },
              })
              .then((response) => {
                console.log("photos.save response:", response);
              })
              .catch((error) => {
                console.log("photos.save error:", error);
              });
          })
          .catch((err) => {
            console.log("photos.getUploadServer err:", err);
          });
      })

      .catch((er) => {
        console.log("photos.createAlbum err:", er);
      });
  }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="12000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  function openBase() {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem
            autoclose
            onClick={() =>
              sharing(
                "share-link",
                null,
                null,
                APP_IMG_SHARING_WALL[globalURLSharing.url]
              )
            }
          >
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() =>
              sharing(
                "copy-link",
                openAlert,
                null,
                APP_IMG_SHARING_WALL[globalURLSharing.url]
              )
            }
          >
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() =>
              sharing(
                "story",
                null,
                null,
                APP_IMG_SHARING_STORIES[globalURLSharing.url]
              )
            }
          >
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={(e) =>
              sharing(
                "share-wall",
                null,
                e,
                APP_IMG_SHARING_WALL[globalURLSharing.url]
              )
            }
          >
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function getRandomImg(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    const result = Math.floor(Math.random() * (max - min)) + min; //Максимум не включается, минимум включается

    globalURLSharing.url = result;
    // setUrlSharing(result);
    setIMGresult(<Div className={`icon${result}`}></Div>);
    // console.log({
    //   // linkWall: APP_IMG_SHARING_WALL[result],
    //   linkStories: APP_IMG_SHARING_STORIES[result],
    //   result: result,
    //   IMGresult: <Div className={`icon${result}`}></Div>,
    // });
  }

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <ConfigProvider isWebView={true}>
      <View activePanel={activePanel} popout={popout} modal={activeModal}>
        <Home
          id="home"
          fetchedUser={fetchedUser}
          go={go}
          setIMGresult={setIMGresult}
          getRandomImg={getRandomImg}
          setActivePanel={setActivePanel}
          publishStories={publishStories}
          getPlatform={getPlatform}
          gotToken={gotToken}
          openAlert={openAlert}
          snackbar={snackbar}
          fetchedUser={fetchedUser}
        />
        <DefaultPanel
          id="default-panel"
          fetchedUser={fetchedUser}
          go={go}
          openAlert={openAlert}
          setActivePanel={setActivePanel}
          snackbar={snackbar}
          getPlatform={getPlatform}
          fetchedUser={fetchedUser}
        />
        <ResultPanel
          id="result-panel"
          fetchedUser={fetchedUser}
          go={go}
          IMGresult={IMGresult}
          urlSharing={urlSharing}
          openBase={openBase}
          getPlatform={getPlatform}
          openAlert={openAlert}
          snackbar={snackbar}
          getGroupId={getGroupId}
        />
        <AdminPanel
          id="admin-panel"
          fetchedUser={fetchedUser}
          go={go}
          openAlert={openAlert}
          snackbar={snackbar}
          getButtonStats={getButtonStats}
          getStats={getStats}
        />
      </View>
    </ConfigProvider>
  );
};

export default App;
